import { Icon, NavBar, TabBar } from 'antd-mobile';
import React from 'react'
import './CSS/shangpin.css'
const Shangpin = (data)=>{
    return <div style={{ position: 'fixed',
                         width: '100%',
                         height:'44.7%'
                          }}>
    <NavBar
      mode="light"
      icon={<Icon type="left" />}
      onLeftClick={() => data.history.push('/')}
    />
        <img src={data.location.state} style={{
        width:'100%',
        height:'400px',  
        }}/>
        <div className="div1">
            <div className="div2">
                <img className="img1" src='./images/dianpu.png'/>
                <p className="p1">店铺</p>
            </div>
            <div className="div2">
                <img className="img1" src='./images/kefu.png'/>
                <p className="p1">客服</p>
            </div>
            <div className="div2">
                <img className="img1" src='./images/biaoxing.png'/>
                <p className="p1">收藏</p>
            </div>
            <div className="div3">
                <img className="img2" src='./images/bom1.png'/>
            </div>
            <div className="div3">
                <img className="img3" src='./images/bom2.png'/>
            </div>
        </div>
            

        </div>
       

}
export default Shangpin;